:root {
  --nav-hight: 60px;
  --nav-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.header {
  flex-grow: 1;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav {
  height: var(--nav-hight);
  align-items: center;
  display: flex;
}

.ul {
  color: #000;
  flex-wrap: wrap;
  padding: 0;
  line-height: 14.6px;
  list-style: none;
  display: flex;
}

.ul .li {
  margin-left: 20px;
}

.ul .li a {
  height: var(--nav-hight);
  color: #000;
  align-items: center;
  text-decoration: none;
  display: flex;
  position: relative;
}

.ul li:first-child {
  margin-left: 0;
}

.ul .li a.is-active:first-child {
  color: var(--nav-color);
}

.ul .li a:hover {
  color: var(--nav-color);
  cursor: pointer;
}

.ul .li a.is-active:after {
  content: "";
  background: var(--nav-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}
